import {
  interGuidanceInfo,
  interRecruitMentInfoAll,
  getByGraduationYear
} from "../../../../utils/employment.url";
export default {
  name: "common",
  components: {},
  data() {
    return {
      userInfo: [],
      careerGuidanceList: [],
      showCareerList:[],
      representList: [],
      recruitmentList: [],
      isViewMore: false,
      loadCount: 2,
    };
  },
  methods: {
    //获取就业指导列表信息
    getGuidanceInfo(id) {
      interGuidanceInfo({
          id: id ? id : ""
        }, "get")
        .then(res => {
          if (res.status == 200) {
            this.careerGuidanceList = res.data;
            this.careerGuidanceList.sort((a, b) => {
              return b.id - a.id
            }) //降序
            this.careerGuidanceList.map((item, index) => {
              item.releaseDate = new Date(item.releaseDate)
                .toLocaleDateString()
                .replace(/\//g, "-");
            });
            this.showCareerList = this.careerGuidanceList;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //获取招聘信息列表
    getRecruitMentInfo() {
      interRecruitMentInfoAll({}, "get")
        .then(res => {
          if (res.status == 200) {
            // 获取招聘信息的最新信息
            if (res.data.lenght < 7) {
              this.recruitmentList = res.data;
            } else {
              this.recruitmentList = res.data.splice(0, 7);
              this.isViewMore = true;
            }

          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //获取就业代表信息列表
    getByGraduationYear(graduationYear) {
      getByGraduationYear({
          graduationYear: graduationYear ? graduationYear : ""
        }, "get")
        .then(res => {
          if (res.status == 200) {
            this.representList = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    load () {
      console.log(this.loadCount)
      this.loadCount +=2;
      this.showCareerList = this.careerGuidanceList.splice(0,this.loadCount)
    },
    viewMore() {
      this.$router.push("recruitmentModel");
      // console.log(itemInfo)
    },
    viewDetail(id) {
      this.$router.push({
        path: `/careerModel/${id}`,
      })
      console.log(id)
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getGuidanceInfo()
    this.getRecruitMentInfo()
    this.getByGraduationYear(2015)
  }
}